// https://firebase.google.com/docs/firestore/manage-data/add-data

export const readData = async (key) => {
    const querySnapshot = await getDocs(collection(dbFirestore, key));
    const data = [];
    querySnapshot.forEach((doc) => {
        console.log(`${doc.id} => ${doc.data()}`);
        const d =  { id: doc.id, data: doc.data()};
        data.push(d);
    });
    return data;
};

export const setData = async (collectionName: string, docPath: string, data: any ) => {
    await setDoc(doc(dbFirestore, collectionName, docPath), {...data, timestamp: serverTimestamp() });
};

export const addData = async (collectionName: string, docPath: string, data: any ) => {
    const newCityRef = doc(collection(dbFirestore, collectionName, docPath));
    await setDoc(newCityRef, {...data, timestamp: serverTimestamp() });
};

export const updateData = async (collectionName: string, docPath: string, data: any ) => {
    const washingtonRef = doc(dbFirestore, collectionName, docPath);
    await updateDoc(washingtonRef, data);
};

export const deleteData = async (collectionName: string, docPath: string ) => {
    await deleteDoc(doc(dbFirestore, collectionName, docPath));
};
